#board {
  width: 300px;
  height: 300px;
  pointer-events: none;
  background: #ddd;
  flex-direction: column;
  justify-content: space-around;
  margin: 40px auto;
  display: flex;
}

#board.my-turn {
  pointer-events: inherit;
  background: #fff;
}

.board-row {
  height: 33%;
  justify-content: space-around;
  display: flex;
}

.board-cell {
  width: 33%;
  background-repeat: no-repeat;
  background-size: contain;
  background-origin: content-box;
  border: 1px solid #000;
  justify-content: center;
  align-items: center;
  padding: 4%;
  display: flex;
}

.board-cell.empty:hover {
  cursor: pointer;
  opacity: .3;
  background-image: url("cross.1f40c540.svg");
  box-shadow: inset 0 0 14px -6px #223c50b3;
}

.board-cell.cross {
  background-image: url("cross.1f40c540.svg");
}

.board-cell.circle {
  background-image: url("circle.deac9d02.svg");
}

#game-ui-title {
  text-align: center;
}

